import { generateFilePath } from '@nextcloud/router';
import NcCheckboxRadioSwitch from '@nextcloud/vue/dist/Components/NcCheckboxRadioSwitch';
export default {
  name: 'ItemPreview',
  components: {
    NcCheckboxRadioSwitch: NcCheckboxRadioSwitch
  },
  props: {
    enforced: {
      type: Boolean,
      default: false
    },
    selected: {
      type: Boolean,
      default: false
    },
    theme: {
      type: Object,
      required: true
    },
    type: {
      type: String,
      default: ''
    },
    unique: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    switchType: function switchType() {
      return this.unique ? 'switch' : 'radio';
    },
    name: function name() {
      return !this.unique ? this.type : null;
    },
    img: function img() {
      return generateFilePath('theming', 'img', this.theme.id + '.jpg');
    },
    checked: {
      get: function get() {
        return this.selected;
      },
      set: function set(checked) {
        console.debug('Changed theme', this.theme.id, checked);

        // If this is a radio, we can only enable
        if (!this.unique) {
          this.$emit('change', {
            enabled: true,
            id: this.theme.id
          });
          return;
        }

        // If this is a switch, we can disable the theme
        this.$emit('change', {
          enabled: checked === true,
          id: this.theme.id
        });
      }
    }
  },
  methods: {
    onToggle: function onToggle() {
      if (this.switchType === 'radio') {
        this.checked = true;
        return;
      }

      // Invert state
      this.checked = !this.checked;
    }
  }
};